import Vue from 'vue'
import dataConfig from '@/config/index'

Vue.filter('setName', (val) => {
	 // + String(val).slice(-1)
	return String(val).slice(0, 1) + '**'
})

// 0-6
// <span>{{String(Balance).length>6?'+':''}}</span>
Vue.filter('spliceNum', (num) => {
	let newNum = String(num)
	if (newNum.length > 6) {
		return newNum.slice(0, 6) + '+'
	} else {
		return num
	}
})

Vue.filter('orderState', (num) => {
	if (num === '') {
		return ''
	}
	num = Number(num)
	switch (num) {
		case -3:
			return '';
		case -1:
			return '';
		case 0:
			return '';
		case 1:
			return '';
		case 2:
			return '';
		case 3:
			return '';
		case 4:
			return '';
		default: ''
	}
})
Vue.filter('orderType', (num) => {
	// 2
	num = Number(num)
	switch (num) {
		case 1:
			return '';
		case 3:
			return '';
		case 4:
			return '';
		case 5:
			return '';
		default: ''
	}
})

Vue.filter('RefundState', (num) => {
	if (!num) {
		return '--'
	}
	num = Number(num)
	switch (num) {
		case 1:
			return '';
		case 2:
			return '';
		case 3:
			return '';
		case 4:
			return '';
	}
})

Vue.filter('numSet', (num) => {
	if (!num) {
		return num
	}
	return parseFloat(Number(num).toFixed(2))
})

Vue.filter('setMoney', (val) => {
	val = Number(val)
	if (val > 10000) {
		let thousand = (val / 10000).toFixed(1)
		return thousand + '+'
	} else {
		return val
	}
})
Vue.filter('setImgPrex', (val) => {
	if (val) {
		return dataConfig.VUE_APP_PREFIX + val
	} else {
		return ''
	}
})

Vue.filter('payTypeOrder', (val) => {
	let num = Number(val)
	if (num) {
		return num === 1 ? '' : num === 2 ? '' : ''
	} else {
		return ''
	}
})

Vue.filter('deliveryType', (val) => {
	let num = Number(val)
	if (num) {
		return num === 1 ? '' : num === 2 ? '' : ''
	} else {
		return ''
	}
})

Vue.filter('setSex', (val) => {
	if (!val) {
		return ''
	}
	let num = Number(val)
	if (num) {
		return num == 0 ? '' : ''
	} else {
		return num
	}
})
Vue.filter('setBuyTime', (val) => {
	let str = val
	if (val) {
		str = val.split(",").join("");
	}
	return str
})
